.card {
    aspect-ratio: 1/1;
    cursor: pointer;
    
    

    .card-content {
        position: relative;
        height: 100%;
        width: 100%;
        overflow: hidden;
        

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.7));
            z-index: 1;
            border-radius: 10px;
            
        }

        img {
            height: 100%;
            width: 100%;
            border-radius: 10px;
            object-fit: cover;
        }

        p {
            position: absolute;
            color: $white;
            bottom: 10px;
            left: 10px;
            z-index: 3;
            
        }
    }
}


