.collapsible-button-container {
    width: 85%;
    margin-top: 30px;
    position: relative;
    
    .collapsible-button {
        background-color: $main-color;
        width: 100%;
        color: $white;
        border: none;
        border-radius: 5px;
        height: 42px;
        text-align: left;
        font-size: 1.33rem;
        padding-left: 15px;

    }
    .arrow{
        cursor: pointer;
        position: absolute;
        right: 10px;
        top: 3px;
        transition: transform 200ms;

        &.rotate {
           transform: rotate(-180deg);
       }
    }
}

.collapsible-content {
    background-color: $light-grey;
    padding: 1rem;
    border-radius: 5px;

    li{
        margin-bottom: 0.1rem;
    }
}
    
.content-parent {
    height: 0;
    overflow: hidden;
    transition: height ease 500ms;
}

@media screen and (max-width: 610px) {

    .collapsible-button-container {
        width: 100%;
        margin-top: 19px;

        .collapsible-button{
            height: 30px;
            font-size: 0.72rem;
        }

        .arrow{
            width: 22px;

        }
        .collapsible-content {
            font-size: 0.67rem;
        }

    }
}

    


