.carousel-container{
    position: relative;
    margin-top: 50px;
   
    .arrow {
        cursor: pointer;
        z-index: 99;
        position: absolute;
        top: 50%;

        transform: translateY(-50%);
        &.next {
            
            right: 20px;
        }
        &.prev {

            left: 20px;
        }
    }
    .paging {
        position: absolute;
        bottom: 25px;
        left: 50%;
        transform: translateX(-50%);
        color: $white;

    }
    .carousel-img {
        width: 100%;
        height: 415px;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 25px;
        }

    }
}

@media screen and (max-width: 775px) {
    .carousel-container{
        .arrow {
            img{
            width: 24px;
        }
    }
        .carousel-img{
            height: 220px;
        }
    }
}